type User = {
  id: string
  name: string
  email: string
  phone: string
  role: 'Sub-Account Owner' | 'Admin' | 'Manager' | 'User'
  status: 'Active' | 'Inactive'
  lastLogin: string
}

type SubAccount = {
  id: string
  name: string
  type: string
  companyId: string
  companyName: string
  status: 'Active' | 'Inactive'
  owner: {
    name: string
    email: string
    phone: string
  }
  branding: {
    primaryColor: string
    secondaryColor: string
  }
  users: User[]
  totalUsers: number
  createdAt: string
  lastActivity: string
  notes: string
}

